.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.center{
  text-align: center;
}
.card{
  border: 1px solid ;
  width: 400px;
  height: 600px;
  margin: 0 auto;
  margin-top: 50px;
}
.img{
  margin: 10px;
  width: 100%;
}
.movieCard{
  display: flex;
  gap:10px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.searchFeild{
  margin: 50px 0;
}
.searchFeild input{
  padding:5px 20px;
  box-shadow: 0 0 0 1px gray;
}
.searchFeild button{
  padding:5px 20px;
  box-shadow: 0 0 0 1px gray;
  background: black;
  color: white;
}
